// 引用axios
import axios from 'axios'
/*import checksign from './checksign.js'*/
// 自定义判断元素类型JS
function toType(obj) {
    return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}
// 参数过滤函数
function filterNull(o) {
    for (var key in o) {
        if (o[key] === null) {
            delete o[key]
        }
        if (toType(o[key]) === 'string') {
            o[key] = o[key].trim()
        } else if (toType(o[key]) === 'object') {
            o[key] = filterNull(o[key])
        } else if (toType(o[key]) === 'array') {
            o[key] = filterNull(o[key])
        }
    }
    return o
}


const CancelToken = axios.CancelToken;
var cancels = []

function apiAxios(method, url, params, success, failure) {
    let source = CancelToken.source()
    // window.document.getElementById("isshow").style.display="block"
    /*  url=checksign.apendSign(url)*/
    if (params) {
        params = filterNull(params)
    }
    let str = window.location.host
    // if (str.indexOf("wechatdev") > -1) {
    //     console.log("wechatdev")
    //     root = root1
    // } else if (str.indexOf("test") > -1) {
    //     console.log("test")
    //     root = root2
    // } else if (str.indexOf("mdwx") > -1) {
    //     console.log("mdwx")
    //     root = root4
    // } else if (str.indexOf("img") > -1) {
    //     console.log("img")
    //     root = root5
    // } else {
    //     console.log("其他")
    //     root = root2
    // }
    console.log(root)
    return new Promise(() => {
        axios({
            method: method,
            url: url,
            data: method === 'POST' || method === 'PUT' || method === 'DELETE' ? params : null,
            params: method === 'GET' ? params : null,
            baseURL: root,
            withCredentials: false,
            cancelToken: source.token
        }).then(function (res) {
            // window.document.getElementById("isshow").style.display = "none"
            if (res.status == 200) {
                if (success) {
                    success(res.data)
                }
                if (!res.data.success) {
                    // Toast({
                    //     message: res.data.msg,
                    //     iconClass: "icon iconfont iconicon-cuowu"
                    // });
                }
            } else {
                if (failure) {
                    failure(res.data)
                } else {
                    errors(res.status)
                }
            }
        }).catch(function (err) {
            if (err && err != "Error: Network Error" && err.message != 1) {
                // Toast({
                //     message: err,
                //     iconClass: "icon iconfont iconicon-cuowu"
                // });
            }
        })
    })
}

function getImg(url) {
    return root + "api/file/download/" + url
}
function getVideo(url) {
    return root + "api/file/download/path/" + url
}
// 返回在vue模板中的调用接口
export default {
    get: function (url, params, success, failure) {
        return apiAxios('GET', url, params, success, failure)
    },
    post: function (url, params, success, failure) {
        return apiAxios('POST', url, params, success, failure)
    },
    put: function (url, params, success, failure) {
        return apiAxios('PUT', url, params, success, failure)
    },
    delete: function (url, params, success, failure) {
        return apiAxios('DELETE', url, params, success, failure)
    },
    getImg,
    getVideo,
    cancel: function () {
        cancels.forEach((item, index) => {
            delete cancels[index]
            item && item(1)
        })
    },
}



