import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('../components/tab/index.vue')
    },
    {
      path: '/Competence',
      name: 'Competence',
      component: () => import("../components/tab/Competence.vue"),
      meta: {
        title: "Competence",
      }
    },
  ]
})



